.ingredient {
  min-width: 10em;
  color: white;
  background-color: #1c1c1c;
  box-shadow: 2px 2px 2px 2px #ccc;
  align-items: center;
  display: inline-block;
  padding: 20px;
  margin: 20px;
}
.buttonClass {
  color: white;
  width: 3em;
  border-radius: 0.5em;
  padding: 1em;
  background-color: inherit;
  outline: none;
  border: none;
}
.buttonClass:active {
  background-color: #00000030;
}
