.Burger {
  width: 100%;
  height: 20em;
  margin-left: auto;
  margin-right: auto;
  overflow: scroll;
  background-color: #1c1c1c;
  color: white;
  box-shadow: 2px 2px 2px 2px #ccc;
  text-align: center;
}

@media (min-width: 1000px) and (min-height: 700px) {
  .Burger {
    width: 600px;
    height: 25rem;
  }
}
@media (min-width: 501px) and (min-height: 401px) {
  .Burger {
    width: 400px;
    height: 25rem;
  }
}
