.ingredientContainer {
  margin-top: 1em;
  text-align: center;
  width: 100%;
}
.price {
  text-align: center;
  font-weight: bold;
  color: red;
}
.ATC {
  color: white;
  background-color: #1c1c1cff;
  padding: 1em;
  outline: none;
  border: none;
  box-shadow: 2px 2px 2px 2px #ccc;
  margin-bottom: 1em;
}
.ATC:disabled {
  background-color: #1c1c1c70;
}
.ATC:active {
  background-color: #000000c0;
}
